/// <reference path='./FightViewBase.ts' />
namespace zt.battle {
    import GComponent = fairygui.GComponent;
    import Sprite = Laya.Sprite;
    import Event = Laya.Event;
    import Rectangle = Laya.Rectangle;
    export class CtrlView extends FightViewBase {
        protected init() {
            zt.ctrl.GameCtrl.intance.on(BattleEvent.ARMY_UPDATE, this, this.onArmyUpdate);
        }

        private onArmyUpdate() {
            this.updateRound();
        }

        private updateRound() {
            this.round.text = `ROUND${this._model.round}`;
        }

        protected draw(): void {
            //
        }

        protected get layout(): number {
            return Layout.CENTER + Layout.TOP;
        }

        protected get offset(): number[] {
            return [FightViewBase.GAP, FightViewBase.GAP, 0, 0];
        }

        private get round() { return this._ui.getChild('round').asTextField }
    }
}
