/// <reference path='./ActBase.ts' />
import Effect = zt.effects.Effect ;
import Handler = Laya.Handler;
import Rectangle = Laya.Rectangle ;
import SysbattleEffectVo = zt.data.SysBattleEffectVo ;
import SysBattlePerformeVo = zt.data.SysBattlePerformeVo;
namespace zt.battle
 {
    export class ActAttack extends ActBase 
    {
        private formAni:Effect ;
        play() 
        {
            let attackX : number = 0 ;
            let attackY : number = 0 ;
            const actname:string = `Attack_${this.fromArmy.FactionID}` ;
            const battleEffect:SysbattleEffectVo = Battle.SysBattleEffectP.getDataByAction(actname);
            const formView = this._fromSide == 'left' ? this.attackerView : this.defenderView ;
            const toView = this._fromSide == 'left' ? this.defenderView : this.attackerView ;
            const attack = formView.getShip(this._fromPos);
            let pos = {"x":0,"y":0};
            if(attack)
            {
                let shipName:string = attack.data.shipName;
                const attackShip:SysBattlePerformeVo = Battle.SysBattlePerformeP.getDataByShip(shipName);
                attackX = attackShip.AttackX;
                attackY = attackShip.AttackY;
                pos.x = attack.shipX >> 0 ; 
                pos.y = attack.shipY >> 0 ;
            }
            const url:string = utils.getResourceURL(this.url) ;
            let effextX = this._fromSide == 'left' ? battleEffect.Lx : battleEffect.Rx ;
            let effextY = this._fromSide == 'left' ? battleEffect.Ly : battleEffect.Ry ;
            let aniX:number =  effextX[0] + pos.x + attackX ;
            let aniY:number =  effextY[0] + pos.y + attackY + 50;
            const isContray  = this._fromSide == 'left' ? false : true ;
            const container = this.container ;
            let recoup:number = this._fromSide == 'left' ? 105 :2;
            let length:number = (utils.getTargetToCenter(aniX,aniY) >> 0) + recoup ;
            const formAni  = zt.effects.createSkillEffect(url,aniX,aniY,isContray,length,container,true);
            formAni.playEffect(60) ;
            formAni.addFrame(battleEffect.Frame,Handler.create(this,()=>
            {
                let angle:number = isContray ?  154:-26;
                // let angle:number = isContray ?  140:-40;
                pos.x = 0 ;
                pos.y = 0 ;
                for(let target of this._to)
                {
                    recoup = this._fromSide == 'left' ? 50 :200;
                    const defend = toView.getShip(target.Pos);
                    if(defend)
                    {
                        let shipName:string = defend.data.shipName;
                        const attackShip:SysBattlePerformeVo = Battle.SysBattlePerformeP.getDataByShip(shipName);
                        attackX = attackShip.HitX ; 
                        attackY = attackShip.HitY ;
                        pos.x = defend.shipX  ;
                        pos.y = defend.shipY ;
                    }
                    aniX = (pos.x + attackX)>>0 ;
                    aniY = (pos.y + attackY + 50)>>0;
                    length = (utils.getTargetToCenter(aniX , aniY) >> 0) + recoup;
                    // const point = utils.getLineVerticalPoint(aniX,aniY);
                    // if(isContray)
                    // {
                    //     point.x = point.x - Math.cos(angle/180*Math.PI) * (length -recoup);
                    //     point.y = point.y + Math.sin(angle/180*Math.PI) * (length -recoup);
                    // }else
                    // {
                    //     point.x = point.x - Math.cos(angle/180*Math.PI) * (length -recoup);
                    //     point.y = point.y + Math.sin(angle/180*Math.PI) * (length -recoup);
                    // }
                    angle = 0 ;
                    if(isContray)
                    {
                        aniX = aniX + Math.cos(angle/180*Math.PI) * (length -recoup) + 100;
                        aniY = aniY - Math.sin(angle/180*Math.PI) * (length -recoup) - 29;
                       
                    }else
                    {
                        aniX = aniX - Math.cos(angle/180*Math.PI) * (length -recoup) +10;
                        aniY = aniY + Math.sin(angle/180*Math.PI) * (length -recoup) - 5;
                    }
                    const toAni =  zt.effects.createSkillEffect(url,aniX,aniY,isContray,length,container,false);
                    // const toAni =  zt.effects.createSkillEffect(url,point.x,point.y,isContray,length,container,false);
                    toAni.playEffect(60);
                }
            }));
        }

        private get url() {
            // return `fight/skill/skill_1_10.json`;
            return `fight/attack/skill.json`;
            // return `fight/attack/${this.fromArmy.FactionID}.json`;
        }
    }
}