/// <reference path='./FightViewBase.ts' />
namespace zt.battle {
    import GComponent = fairygui.GComponent;
    import Sprite = Laya.Sprite;
    import Event = Laya.Event;
    import Rectangle = Laya.Rectangle;
    export class WaveView extends FightViewBase {
        updateWave() {
            if (!this._model.Defender) return
            const curr = this._model.Defender.TotalCount - this._model.Defender.Count + 1;
            this.txt.text = `WAVE ${curr}/${this._model.Defender.TotalCount}`

        }
        protected init() {
            zt.ctrl.GameCtrl.intance.on(BattleEvent.ARMY_UPDATE, this, this.onArmyUpdate);
        }

        private onArmyUpdate() {
            this.updateWave();
        }

        protected draw(): void {
            //
        }

        protected get layout(): number {
            return Layout.RIGHT + Layout.TOP;
        }

        protected get offset(): number[] {
            // tslint:disable-next-line:no-magic-numbers
            return [FightViewBase.GAP + 70, FightViewBase.GAP, 0, 0];
        }

        private get txt() { return this._ui.getChild('txt').asTextField }
    }
}
