namespace zt.battle {
    import GComponent = fairygui.GComponent;
    export class FightViewBase {
        static GAP = 0;
        protected _ui: GComponent;
        protected _controller: Controller;
        protected _model: Model;

        constructor(ui: GComponent, controller: Controller, model: Model) {
            this._ui = ui;
            this._controller = controller;
            this._model = model;
            this.init();
            this.draw();
        }

        dispose() {
            if (this._ui) {
                this._ui.dispose();
                this._ui.displayObject.removeSelf();
                this._ui = null;
            }
        }

        protected draw(): void {
            this._ui.displayObject.visible = false;
        }

        protected init() {
            //
        }

        protected get layout(): number {
            return Layout.NONE;
        }

        protected get width(): number {
            return this._ui.width;
        }

        protected get height(): number {
            return this._ui.height;
        }

        protected get offset(): number[] {
            return [0, 0, 0, 0];
        }

        resize(): void {
            Layers.getLayer('app').addChild(this._ui.displayObject);
            const [offsetTop, offsetRight, offsetBottom, offsetLeft] = this.offset;
            const left: boolean = (this.layout & Layout.LEFT) > 0;
            const top: boolean = (this.layout & Layout.TOP) > 0;
            const right: boolean = (this.layout & Layout.RIGHT) > 0;
            const bottom: boolean = (this.layout & Layout.BOTTOM) > 0;
            const center: boolean = (this.layout & Layout.CENTER) > 0;
            const centerV: boolean = (this.layout & Layout.CENTER_V) > 0;
            left && (this._ui.displayObject.x = 0);
            top && (this._ui.displayObject.y = 0);
            right && (this._ui.displayObject.x = Laya.stage.width - this.width);
            bottom && (this._ui.displayObject.y = Laya.stage.height - this.height);
            center && (this._ui.displayObject.x = (Laya.stage.width - this.width) / 2);
            centerV && (this._ui.displayObject.y = (Laya.stage.height - this.height) / 2);
            this._ui.displayObject.y += offsetTop;
            this._ui.displayObject.x -= offsetRight;
            this._ui.displayObject.y -= offsetBottom;
            this._ui.displayObject.x += offsetLeft;
        }
    }

    export enum Layout {
        NONE = 0b1,
        TOP = 0b10,
        LEFT = 0b100,
        BOTTOM = 0b1000,
        RIGHT = 0b10000,
        CENTER = 0b100000,
        CENTER_V = 0b1000000,
    }
}