/// <reference path='./MainControllerBase.ts' />
namespace zt.battle {
    export class BattleTestController extends MainControllerBase {
        private _updateList: any[];
        private _data;
        private _time: number;

        start() {
            this._data = this._model.replayData;
            this._time = this._data['Battle.ready']['_time'];
            this.onEnterFleet(this._data['Battle.ready']);
            this._updateList = this._data['updateDatas'].concat();
            this.playUpdateDatas();
        }

        private playUpdateDatas() {
            if (this._updateList.length == 0) {
                this.playFinish(this._data['Battle.finish'])
                return;
            }
            const data = this._updateList.shift();
            this.playUpdateData(data);
        }

        private playUpdateData(data) {
            const delay = data.Time - this._time;
            Laya.timer.once(delay, this, () => {
                this.playUpdateDatas();
                this.onBattleUpdate.runWith(data);
            });
        }

        private playFinish(data) {
            // this.onBattleFinish.runWith(data);
        }

        dispose() {
            super.dispose();
        }
    }
}