/// <reference path='./ActBase.ts' />
namespace zt.battle {
    import Effect = zt.effects.Effect ;
    export class ActAddBuff extends ActBase {
        //[{"To":[{"Type":0,"Pos":2}],"ActVar":-198,"Act":"DecHp"}]
        play() {
            this._to.forEach(ele => {
                const toArmy = this.getToArmyData(ele.Type);
                toArmy.addBuff(ele.Pos, this.ActVar);
                this.playBuff(ele.Pos);
            })
        }

        playOnce() {
            //buff
        }

        //buff
        private playBuff(pos) {
         
            const actname:string = `Buff_${this.ActVar}` ;
            const battleEffect:SysbattleEffectVo = Battle.SysBattleEffectP.getDataByAction(actname);
            const url:string = utils.getResourceURL(this.buffUrl);
            const toAni:Effect = zt.effects.createEffect(url,AppEnum.battle)
            const container = this.container ;
            const formView = this._toSide == 'left' ? this.attackerView : this.defenderView ;
            const attack = formView.getShip(pos);
            let point = {"x":0,"y":0};
            if(attack)
            {
                let shipName:string = attack.data.shipName;
                point.x =  attack.shipX ;
                point.y =  attack.shipY ;
            }
            let effextX = this._fromSide == 'left' ? battleEffect.Lx : battleEffect.Rx ;
            let effextY = this._fromSide == 'left' ? battleEffect.Ly : battleEffect.Ry ;
            point.x = point.x  + effextX[0];
            point.y = point.y  + effextY[0];
            const raisePoint = {'x':point.x,'y':point.y - 60};
            const points = {'x':point.x,'y':point.y + 20};
            const raiseParam = getRaiseParam(points,raisePoint,false);
            const buffTxt:string = zt.utils.getResourceURL(this.buffTxtUrl);
            Tips.showRaisingImages(buffTxt,raiseParam);
            toAni.playEffect(container,point.x , point.y );
        }

        private get buffUrl() {
            return `fight/buff/1.json`;
        }

        private get buffTxtUrl() {
            return `fight/icon/battleBuffText/${this.ActVar}.png`;
        }
    }
}