/// <reference path='./FightViewBase.ts' />
namespace zt.battle {
    import GComponent = fairygui.GComponent;
    import Sprite = Laya.Sprite;
    import Event = Laya.Event;
    import Rectangle = Laya.Rectangle;
    export class LoadingView extends FightViewBase {
        updateProgress(val: number) {
            this.progressbar.update(val);
        }

        protected init() {
            //
        }
        protected draw(): void {
            //
        }

        protected get layout(): number {
            return Layout.CENTER + Layout.CENTER_V;
        }

        protected get offset(): number[] {
            return [FightViewBase.GAP, FightViewBase.GAP, 0, 0];
        }

        private get progressbar(): fairygui.GProgressBar { return this._ui.getChild('bar') as fairygui.GProgressBar }

    }
}
