namespace zt.battle {
    import UIController = zt.app.UIController ;
    import AppBase = zt.app.AppBase ;
    /**
    * Controller
    * @author wildfirecode
    * @since 0.0.3
    */
    export class Controller extends UIController
    {
        private _loading: LoadingController;
        private _actController: ActController;
        private _mainController: MainControllerBase;
        private _view:View ;
        private _registerBattle:object ;
        constructor(app: AppBase) 
        {
            super(app); 
        }

        public init():void
        {
            this._registerBattle = {};
            this._registerBattle[BattleMode.NORMAL] = MainController;
            this._registerBattle[BattleMode.REPLAY] = ReplayMainController;
            this._registerBattle[BattleMode.TEST] = BattleTestController;
        }
        
        start() {
            this._view = getViewByModuleName(AppEnum.battle) as View;
            const  model = this.model as Model ;
            this._loading = new LoadingController();
            this._actController = new ActController(this._view, model );
            this._mainController = new this._registerBattle[model.mode](model, this._view) 
            this._mainController.onBattleFinish = Laya.Handler.create(this, this.onBattleFinish);
            this._mainController.onBattleUpdate = Laya.Handler.create(this, this.onBattleUpdate, null, false);
            this._mainController.init();
            FloatTool.init();
            this.startLoading();
            this.disposeLoading();

            this._mainController.start();
        }

        onCloseClick() {
            const  model = this.model as Model ;
            if (model.mode == BattleMode.NORMAL)
            {
                leaveBattle(model.getParams());
            }
            if(model.getParams().Type=='DailyInstance')
            {
                App.load('dailyInstance');
            }
            if(model.getParams().Type=='NormalInstance')
            {
                App.load('normalInstance');
            }
            this.close();
        }

        dispose() {
            FloatTool.dispose();
            this._mainController.dispose();
            this._actController.dispose();
        }

        private onBattleFinish(data) {
            // tslint:disable-next-line:no-magic-numbers
            App.updateMask('#040f15', 0.95);
            App.load('battleResult', { onClose: Laya.Handler.create(this, this.onBattleResultClose), Content: data });
        }

        private onBattleUpdate(data) {
            if (Array.isArray(data.Content.List))
                data.Content.List.forEach(act => {
                    this._actController.update(act);
                });
        }

        private onBattleResultClose() {
            this.close();
            // this._app.visible = false;
            App.resetMaskColor();
        }

        private startLoading() {
            this._loading.start();
        }

        private disposeLoading() {
            this._view.disposeLoading();
        }
    }
}