/// <reference path='./ActBase.ts' />
namespace zt.battle {
    import Effect = zt.effects.Effect ;
    export class ActDead extends ActBase {
        play() {
            const toArmy = this.getToArmyData(this._to[0].Type);
            toArmy.shipDead(this._to[0].Pos);
            this.playEffect();
        }

        playEffect() {
            const url:string = utils.getResourceURL(this.url);
            const container:Sprite =  this.attackerView.parent as Sprite;
            for(let target of this._to)
            {
                const point =  this.getToPoint(target.Pos)
                const toAni :Effect  = zt.effects.createEffect(url,AppEnum.battle,true);
                toAni.playEffect(container, point.x -150, point.y - 150,false,Laya.BlendMode.ADD,null,30) ;
            }
        }

        private get url() {
            return 'fight/dead/dead.json';
        }
    }
}