// tslint:disable:no-magic-numbers
namespace zt.battle {
    export class ArmyView extends Sprite {
        private _type;
        private _shipList: { [key: string]: ShipView };
        private _army: Army;
        private _shipLayer: Sprite;
        private _underShipLayer: Sprite;
        private _overShipLayer: Sprite;
        private _bgs: fairygui.GComponent[];

        constructor(type) {
            super();
            this._type = type;
            this._underShipLayer = new Sprite();
            this._shipLayer = new Sprite();
            this._overShipLayer = new Sprite();
            this.addChildren(this._underShipLayer, this._shipLayer, this._overShipLayer);
            this.name = 'army';

        }

        setData(army: Army) {
            this.removeShips();
            this.removeBgs();
            this._army = army;
            this._army.on(BattleShipEvent.DEAD, this, this.onShipDead);
            this.createShips();
            this.addBgs();
            this.setMask();
        }

        private removeBgs() {
            if (!this._bgs) return;
            this._bgs.forEach(element => {
                element.dispose();
            });
            this._bgs = null;
        }

        private addBgs() {
            this._bgs = [];
            for (let index = 1; index <= 9; index++) {
                const ship = this._army.getShip(index);
                const pos = getPos(this._army.side, index);
                const bg = fairygui.UIPackage.createObject('battle', 'shipBg_' + this._army.side) as fairygui.GComponent;
                this._bgs.push(bg);
                bg.displayObject.pos(-99 + pos.x, -10 + pos.y);
                bg.getController('c1').selectedIndex = 0;
                if (ship) {
                    bg.getController('c1').selectedIndex = getPosFrame(ship.ObjectType, ship.Grade);
                }
                if (this._type == 'defender') {
                    bg.displayObject.pos(-50 + pos.x, -38 + pos.y);
                }
                this._underShipLayer.addChild(bg.displayObject);
            }
        }

        /**
         *
         */
        public shakeShip($to:any[],$act:string,$factionID:number = -1):void
        {
            for(let obj of $to)
            {
                const ship:ShipView =  this._shipList[obj.Pos];
                if(ship)
                {
                    ship.shake($act,$factionID);
                }
            }
        }

        private onShipDead(pos) {
            this.removeShip(pos);
        }

        checkEnterFleet(isReJoin) {
            if (this._army.enterFlag) return;
            for (const key in this._shipList) {
                this.shipEnter(this._shipList[key], isReJoin);
            }
        }

        private setMask() {
            if (this._underShipLayer.mask) return;
            this._underShipLayer.mask = getMask(this._army.side);
            this._overShipLayer.mask = getMask(this._army.side);
        }

        private removeShips() {
            if (!this._shipList) return;
            for (const pos in this._shipList) {
                this.removeShip(pos);
            }
        }

        removeShip(pos) {
            this._shipList[pos].dispose();
            delete this._shipList[pos];
        }

        getShip(pos) {
            return this._shipList[pos];
        }

        private shipEnter(ship: ShipView, isReJoin) {
            const pos = getPos(this._army.side, ship.data.Pos);
            if (isReJoin)
                ship.pos(pos.x, pos.y);
            else
                Laya.Tween.to(ship, { x: pos.x, y: pos.y }, 1000);
        }

        private createShips() {
            this._shipList = {};
            for (const ship of this._army.ShipList) {
                if(ship.Hp>0)//
                {
                    const shipView = new ShipView(ship);
                    this._shipList[ship.Pos] = shipView;
                    this._shipLayer.addChild(shipView);
                    const pos = getFromPos(this._army.side, ship.Pos);
                    shipView.pos(pos.x, pos.y);
                }
            }
        }

        dispose() {
            //
        }
        get army() { return this._army }
        get underShipLayer() { return this._underShipLayer }
        get overShipLayer() { return this._overShipLayer }
    }
}