// tslint:disable:no-magic-numbers
namespace zt.battle {
    export class DamageNums extends Laya.Sprite {
        private _txt: fairygui.GTextField;
        private _type;
        constructor(type) {
            super();
            this._type = type;
            this.init();
        }

        private init() {
            this._txt = new fairygui.GBasicTextField();
            this._txt.font = this.font;
            this._txt.color = '#ffffff';
            this.addChild(this._txt.displayObject);
        }

        set data(num: number) {
            this._txt.text = num.toString();
            this._txt.displayObject.pivot(this._txt.width / 2, this._txt.height / 2);
        }

        play() {
            const toY = this.y - 70 ;
            Laya.Tween.to(this, {
                y: toY
            }, 300, null, Laya.Handler.create(this, this.onAnicomplete));
        }

        private onAnicomplete() {
            Laya.Tween.to(this,
                { alpha: 0, y: this.y - 10 },
                300, null, Laya.Handler.create(this, () => {
                    this.removeSelf();
                    this.oncomplete();
                }), 200);
        }

        private oncomplete() {
            this.event(Laya.Event.COMPLETE)
        }

        private get font() {
            switch (this._type) {
                case DamageNumsType.DAMAGE:
                case DamageNumsType.SHIELD:
                    return 'ui://orii4jisilkm3x'

                case DamageNumsType.CRIT:
                    return 'ui://orii4jisilkm3v'
            }
        }
    }
}