// tslint:disable:no-magic-numbers
/// <reference path='./ActBase.ts' />
namespace zt.battle {
    import Effect = zt.effects.Effect ;
    export class ActTurn extends ActBase
    {
        private _faction:number = 0;
        private static _ins:ActTurn ;
        private curr :Effect;
        private _type:string ;
        private _pos:number;
        constructor()
        {
            super();
        }
       

        /**
         * @param type 
         * @param faction 
         */
        public play(type:string = 'attack',$faction:number = -1) 
        {
            if(this._to && !!this._to[0] == false || this._type == type &&
               this._pos == this._to[0].Pos && 
               this.curr && this.curr.isPlaying)
            {
                return ;
            }
            type = type.toLocaleLowerCase();
            this._type = type ;
            this._pos =  this._to[0].Pos ;
            this._faction = $faction == -1 ? this._faction:$faction ;
            const url:string = utils.getResourceURL(`fight/turn/${type}/${this._faction}.json`);
            zt.effects.resyleEffectGroup(this.curr);
            this.curr = zt.effects.createEffect(url,AppEnum.battle,true);
            const toPoint = this.getToPoint(this._pos);
            const x:number  = this._toSide == 'left'? toPoint.x - 206 : toPoint.x - 142 ;
            const y:number  =  this._toSide == 'left'? toPoint.y - 87 : toPoint.y - 96;
            this.curr.playEffect( this._toView.underShipLayer,x,y,false,null,null,80);
        }

        public get name():string
        {
            return 'ActTurn';
        }

        public static get ins():ActTurn
        {
            if(!ActTurn._ins)
            {
                ActTurn._ins = new ActTurn();
            }
            return ActTurn._ins;
        }

        static dispose() {
            if(ActTurn._ins)
            {
                zt.effects.resyleEffectGroup(ActTurn._ins.curr);
            }
        }
    }
}