namespace zt.battle {
    export class MainControllerBase {
        onBattleFinish: Laya.Handler;
        onBattleUpdate: Laya.Handler;
        protected _model: Model;
        protected _view: View;
        constructor(model: Model, view: View) {
            this._model = model;
            this._view = view;
        }

        init() {
            //
        }

        protected onBattleReady(data) {
            const result = this._model.updateArmy(data);
            if (this._model.isReJoin||1)
                this.checkEnterFleet(result, true);
        }

        protected onEnterFleet(data) {
            this.checkEnterFleet(this._model.updateArmy(data));
        }

        private checkEnterFleet(result, isReJoin = false) {
            if (result.Attacker) {
                this._view.attacker.setData(this._model.Attacker);
                this._view.attacker.checkEnterFleet(isReJoin);
            }
            if (result.Defender) {
                this._view.defender.setData(this._model.Defender);
                this._view.defender.checkEnterFleet(isReJoin);
            }
            this._model.checkEnterFleet();
        }

        start() {
            //
        }
        dispose() {
            //
        }
    }
}