// tslint:disable:no-magic-numbers
namespace zt.battle {
    import Point = Laya.Point ;
    export const getFrame = (FactionID) => {
        switch (FactionID) {
            case 1:
                return 0;
            case 2:
                return 1;
        }
        return 2;
    }
    export const getPosFrame = (objectType, grade) => {
        if (objectType == ObjectType.Fleet || objectType == ObjectType.MobFleet) {
            return 7 + grade;
        }
        if (objectType == ObjectType.Ship || objectType == ObjectType.Mob) {
            return grade;
        }
        return 0;
    }


}