namespace zt.battle {
    import DamageNum = zt.effects.DamageNum ;
    export class ActBase 
    {
        model: Model;
        ActVar: number;
        fromArmy: Army;
        toArmy: Army;
        attacker: Army;
        defender: Army;
        Crit:number;
        attackerView: ArmyView;
        defenderView: ArmyView;
        container: Sprite;
        protected _fromView: ArmyView;
        protected _toView: ArmyView;
        protected _fromSide: string;
        protected _toSide: string;
        protected _fromPoint: { x, y };
        protected _mainView: Sprite;
        protected _fromPos: number;
        protected _to: ActTarget[];
        set fromView(val: ArmyView) { this._fromView = val }
        set toView(val: ArmyView) { this._toView = val }
        set mainView(val: Sprite) { this._mainView = val }
        set fromPos(val: number) { this._fromPos = val }
        set to(val: ActTarget[]) { this._to = val }
        set fromSide(val) { this._fromSide = val }
        set toSide(val) 
        {
             this._toSide = val 
        }
        set fromPoint(val) 
        {
             this._fromPoint = val ;
        }

        protected getToPoint(pos: number) {
            return getPos(this._toSide, pos)
        }

        protected getToArmyData(type) {
            return type == 1 ? this.attacker : this.defender;
        }

        get name():string
        {
            return 'type';
        }
        
        play($type?:string,$faction?:number) {
            //
        };


        /**
         * 
         * @param  
         * @param  
         */
        public playDamage($damageType:number,$isAdd:boolean = false):void
        {
            const toPoint = this.getToPoint(this._to[0].Pos);
            const damage = DamageNum.getDamageNum(this.ActVar,$damageType,$isAdd)
            damage.pos(toPoint.x, toPoint.y - 40);
            this._toView.addChild(damage);
            damage.play($damageType);
        }
    }
}