namespace zt.battle {
    export const getPos = (side, pos): { x, y } => {
        if (side == 'left') {
            const list = [
                { x: 556, y: 266 },
                { x: 709, y: 437 },
                { x: 875, y: 602 },
                { x: 324, y: 260 },
                { x: 471, y: 433 },
                { x: 648, y: 600 },
                { x: 246, y: 436 },
                { x: 412, y: 608 },
                { x: 566, y: 766 }
            ];
            let position = pos - 1 ;
            position = position== -1 ? 0 : position ;
            return list[position];
        }
        const list = [
            { x: 1040, y: 337 }, //1
            { x: 1199, y: 503 },//2
            { x: 1390, y: 666 },//3
            { x: 1137, y: 163 },//4
            { x: 1276, y: 328 },//5
            { x: 1449, y: 497 },//6
            { x: 1364, y: 164 },//7
            { x: 1500, y: 336 },//8
            { x: 1662, y: 500 }//9
        ];
        let position = pos - 1 ;
        position = position== -1 ? 0 : position ;
        return list[position];
    }

    /**
     * pos
     * @param side 
     * @param pos 
     */
    export const getFromPos = (side, pos) => {
        const offset = { x: 860, y: 470 };
        const posConfig = getPos(side, pos);
        const random = Math.random() * 500;
        if (side == 'left') {
            return { x: posConfig.x - (offset.x + random), y: posConfig.y + (offset.y + random) }
        }
        return { x: posConfig.x + (offset.x + random), y: posConfig.y - (offset.y + random) }
    }

    
    export const getshipFirepoint = (side)=>
    {
        return side == 'left' ? { x: -185, y: -145 } : { x: -126, y: -175 };
    }
}