namespace zt.battle {
    import UIModel = zt.app.UIModel ;
    /**
    * model
    * @author wildfirecode
    * @since 0.0.3
    */
    export class Model extends UIModel
    {
        private _round;
        private _maxRound;
        private _Attacker: Army;
        private _Defender: Army;
        constructor(params) 
        {
            super(params);
            this._params = params;
        }

        updateArmy(data): { Attacker?, Defender?} 
        {
            const result: { Attacker?, Defender?} = {};
            this._round = data.Round;
            this._maxRound = data.MaxRound;
            if (data.Attacker) {
                this._Attacker = new Army(data.Attacker, getAttackerSide(this.type));
                this._Attacker.enterFlag = false;
                result['Attacker'] = this._Attacker;
            }
            if (data.Defender) {
                this._Defender = new Army(data.Defender, getDefenderSide(this.type));
                this._Defender.enterFlag = false;
                result['Defender'] = this._Defender;
            }
            zt.ctrl.GameCtrl.intance.event(EnumEventName.ARMY_UPDATE);
            return result;
        }


        updateRound(round) 
        {
            this._round = round;
            zt.ctrl.GameCtrl.intance.event(EnumEventName.ARMY_UPDATE);
        }

        checkEnterFleet() 
        {
            if (this._Attacker)
                this._Attacker.enterFlag = true;
            if (this._Defender)
                this._Defender.enterFlag = true;
        }

        getParams() 
        {
            return {
                Type: this.type,
                ActivityID: getActivityID(this.type),
                BattleID: this.battleID
            }
        }

        init() 
        {
            //
        }

        dispose()
        {
            //
        }
        
        get round(): number { return this._round };
        // get time(): number { return this._time };
        get Attacker(): Army { return this._Attacker };
        get Defender(): Army { return this._Defender };
        get type(): string { return this._params.battle.type };
        get battleID(): string { return this._params.battle.battleID };
        get mode(): number { return this._params.mode };
        get isReJoin(): boolean { return this._params.isReJoin };
        get replayData(): string { return this._params.replay };

    }
}