namespace zt.battle {
    export const getMask = (side) => {
        const d1 = 484;
        const d2 = 1438;
        const d3 = 950;
        const d4 = 1920;

        const mask = new Sprite();
        // mask.alpha = 0.3;
        if (side == 'left') {
            mask.graphics.drawPoly(0, 0, [0, 0, d1, 0, d2, d3, 0, d3], '#00ff00');
        }
        else {
            mask.graphics.drawPoly(d1, 0, [0, 0, d4 - d1, 0, d4 - d1, d3, d4 - d1 - d1, d3], '#ff0000');
        }
        return mask;
    };
}