// tslint:disable:no-magic-numbers
namespace zt.battle {
    import Sprite = Laya.Sprite;
    import GProgressBar = fairygui.GProgressBar;
    import GComponent = fairygui.GComponent ;
    import GList = fairygui.GList ;
    import Effect = zt.effects.Effect ;
    import Tween = Laya.Tween ;
    import Handler = Laya.Handler ;
    import GObject = fairygui.GObject;
    import Buff = zt.battle.Buff;
    export class ShipView extends Sprite {
        private _data: Ship;
        private _ship: Effect ;
        private _ui: fairygui.GComponent;
        private bufflist : GList ;
        private hpBar:GProgressBar ;
        private shieldBar:GProgressBar;
        private energyBar:GProgressBar ;
        private angerBar:GProgressBar ;
        private hpBarController:fairygui.Controller;
        private _tween:Tween ;
        private _startX:number;
        private _explosion:Effect;
        constructor(data: Ship) {
            super();
            this._data = data;
            this.initView();
            this.initEvent();
        }

        private initView() 
        {
            const model = getModelByModuleName(AppEnum.battle) as Model;
            this._ui = fairygui.UIPackage.createObject('battle', 'ship') as fairygui.GComponent;
            this._ui.displayObject.pos(-95, -35);
            this.addChild(this._ui.displayObject);
            this.bufflist =  this._ui.getChild('bufflist').asList ; 
            this.hpBar= this._ui.getChild('hp').asProgress ;
            this.shieldBar = this._ui.getChild('shield').asProgress ;
            this.energyBar = this._ui.getChild('energy').asProgress ;
            this.angerBar = this._ui.getChild('anger').asProgress ;
            this.hpBarController = this.hpBar.getController('c1');
            if (this._data.side == 'right') 
            {
                this._ui.displayObject.pos(-35, -68);
            }
            this.bufflist.visible = false;
            this.angerBar.visible = false;
            const url:string = utils.getResourceURL(this._data.getUrl());
            if(!this._ship)
            {
                this._ship = zt.effects.createEffect(url,AppEnum.battle,false);
            }
            this.bufflist.itemRenderer = Handler.create(this,this.renderBuff,null,false);
            this._ship.scaleX = this.scaleY = 0.7;
            const firePoint = getshipFirepoint(this._data.side);
            this._ship.playEffect(this,firePoint.x,firePoint.y,true);
            this._startX = firePoint.x ;
            FloatTool.add(this._ship);
            this.onHpUpdate();
            this.onShieldUpdate();
            this.onEnUpdate();
            this.onBuffUpdate();  
        }

        private renderBuff(index:number,obj:GObject):void
        {
            const buffIcon = obj as GComponent ;
            const buff:Buff = this._data.BuffList[index];
            const url:string=  zt.utils.getResourceURL(`fight/icon/battleBuff/${buff.buffID}.png`);
            const icon:Laya.Texture = Laya.loader.getRes(url);
            buffIcon.displayObject.graphics.drawTexture(icon); 
        }

        private initEvent() {
            this._data.on(BattleShipEvent.HP_UPDATE, this, this.onHpUpdate);
            this._data.on(BattleShipEvent.SHIELD_UPDATE, this, this.onShieldUpdate);
            this._data.on(BattleShipEvent.EN_UPDATE, this, this.onEnUpdate);
            this._data.on(BattleShipEvent.BUFF_ADD, this, this.onBuffUpdate);
            this._data.on(BattleShipEvent.BUFF_REMOVE, this, this.onBuffUpdate);
        }

        private onHpUpdate() 
        {
            this.hpBar.update(Math.ceil(this._data.Hp / this._data.MaxHp * 100));
            // this.hpTxt.text = `${this._data.Hp} / ${this._data.MaxHp} `;
        }

        private onShieldUpdate() 
        {
            this.shieldBar.update(Math.ceil(this._data.Shield / this._data.MaxShield * 100));        
            // this.shieldTxt.text = `${this._data.Shield} / ${this._data.MaxShield} `;
        }
        private onBuffUpdate() 
        {
            this.bufflist.visible = this._data.BuffList.length > 0;
            this.bufflist.numItems = this._data.BuffList.length;

        }

        private onEnUpdate() 
        {
            const percent = Math.ceil(this._data.En / this._data.MaxEn * 100);
            this.energyBar.update(percent);
        }

        dispose() {
            this.removeSelf();
            if(this._ship)
                zt.effects.resyleEffectGroup(this._ship)
        }

        /**
         *
         */
        public shake($attackType:string,$faction:number = -1):void
        {
            const isleft = this._data.side == 'left' ;
            const offsetX = isleft ? -10 : 10;
            const toX = this._startX + offsetX ;
            this._tween = Laya.Tween.to(this._ship , {x: toX}, 100, Laya.Ease.sineIn, Laya.Handler.create(this, this.backTo));
        }

        private backTo():void
        {
            const toX =  this._startX ;
            this._tween.clear();
            this._tween = Laya.Tween.to(this._ship , {x: toX}, 200, Laya.Ease.sineIn, Laya.Handler.create(this, this.clear));
        }

        public get shipX():number
        {
            return this._ship ?this._ship.x + this.x : 0;
        }

        public get shipY():number
        {
            return this._ship ? this._ship.y + this.y : 0;
        }

        private clear():void
        {
            this._tween.clear();
        }

        get data() { return this._data }    
    }
}