/// <reference path='./FightViewBase.ts' />
namespace zt.battle {
    import GComponent = fairygui.GComponent;
    import Sprite = Laya.Sprite;
    import Event = Laya.Event;
    import Rectangle = Laya.Rectangle;
    export class NameRightView extends FightViewBase {
        protected init() {
            zt.ctrl.GameCtrl.intance.on(BattleEvent.ARMY_UPDATE, this, this.onArmyUpdate);
        }
        protected draw(): void {
            //
        }

        private onArmyUpdate() {

            let data: Army;
            if (getAttackerSide(this._model.type) == 'right') {
                data = this._model.Attacker;
            }
            else {
                data = this._model.Defender;
            }
            if (!data) return;
            this.name.text = data.Name;
            this.ctrl.selectedIndex = this.getFrame(data.FactionID);
            if (data.FactionID == 0) {
                this.loader.url = utils.getResourceURL('mobavatar/1.png');
            } else {
                this.loader.url = utils.getResourceURL('roleavatar/' + data.AvatarID + '.png');
            }
        }

        private get loader() { return this._ui.getChild('loader').asLoader }
        protected get layout(): number {
            return Layout.RIGHT + Layout.BOTTOM;
        }

        protected get offset(): number[] {
            return [FightViewBase.GAP, FightViewBase.GAP, 0, 0];
        }

        private getFrame(FactionID) {
            switch (FactionID) {
                case 1:
                    return 0;
                case 2:
                    return 1;
            }
            return 2;
        }

        private get fleetName() { return this._ui.getChild('fleetName').asTextField }
        private get name() { return this._ui.getChild('name').asTextField }
        private get ctrl() { return this._ui.getController('c1') }

    }
}
