namespace zt.battle 
{
    export class Ship extends Laya.EventDispatcher {
        ID: number;
        ObjectType: string;
        SysID: number;
        Pos: number
        Hp: number
        MaxHp: number
        Shield: number
        MaxShield: number;
        En: number;
        MaxEn: number;
        Level: number;
        Grade: number;
        Star: number;
        SkillList: Skill[];
        BuffList: Buff[];
        side;
        fid;
        private _shipName:string ;
        private _buffIDList:number[];
        constructor(data, side, fid) 
        {
            super();
            this.side = side;
            this.fid = fid;

            for (const key in data) {
                if (data.hasOwnProperty(key)) {
                    this[key] = data[key];
                }
            }
            this.SkillList = [];
            this.BuffList = [];
            this._buffIDList = [];
            if (data.SkillList)
                for (const skill of data.SkillList) {
                    this.SkillList.push(new Skill(skill));
                }

            if (data.BuffList)
                for (const buff of data.BuffList) {
                    this.BuffList.push(new Buff(buff));
                }
        }

        addBuff(id) 
        {
            const index = this._buffIDList.indexOf(id);
            if(index == -1)
            {
                this._buffIDList.push(id);
                this.BuffList.push(new Buff(id))
                this.event(BattleShipEvent.BUFF_ADD);
            }
        }

        removeBuff(id) 
        {
            const index = this._buffIDList.indexOf(id);
            if(index != -1)
            {
                this.BuffList.splice(index,1);
                this._buffIDList.splice(index,1);
            }
            this.event(BattleShipEvent.BUFF_REMOVE);
        }

        getUrl() {
            const sidePrefix = this.side == 'left' ? 'r' : 'l';
            let type = 'f';
            if (this.ObjectType == ObjectType.Ship || this.ObjectType == ObjectType.Mob) {
                type = 's'
            }
            this._shipName = `${type}${this.SysID}_${sidePrefix}`;
            return `fight/ship/${type}${this.SysID}-${sidePrefix}.json`;   
        }

        updateHp(hp: number) {
            this.Hp += hp;
            this.event(BattleShipEvent.HP_UPDATE);
        }

        updateEn(en: number) {
            this.En += en;
            this.event(BattleShipEvent.EN_UPDATE);
        }

        public get shipName():string
        {
            return this._shipName;
        }

        updateShield(shield: number) {
            this.Shield += shield;
            this.event(BattleShipEvent.SHIELD_UPDATE);
        }
    }
}