namespace zt.battle {
    export class Army extends Laya.EventDispatcher {
        private _ShipList: Ship[];
        private _PosHash: { [pos: string]: Ship }
        private _side: string;
        constructor(data, side) {
            super();
            this._side = side;
            for (const key in data) {
                if (data.hasOwnProperty(key)) {
                    this[`_${key}`] = data[key];
                }
            }
            this._ShipList = [];
            this._PosHash = {};
            for (const ship of data.ShipList) {
                const shipData = new Ship(ship, this._side, this._FactionID);
                this._ShipList.push(shipData);
                this._PosHash[shipData.Pos] = shipData;
            }

        }

        updateShipHp(pos, hpVar) {
            if (this._PosHash[pos])
                this._PosHash[pos].updateHp(hpVar);
        }
        updateShipEn(pos, enVar) {
            if (this._PosHash[pos])
                this._PosHash[pos].updateEn(enVar);
        }

        updateShipShield(pos, attrVar) {
            if (this._PosHash[pos])
                this._PosHash[pos].updateShield(attrVar);
        }

        shipDead(pos) {
            this._PosHash[pos] = null;
            this.event(BattleShipEvent.DEAD, pos);
        }

        addBuff(pos, attrVar) {
            this._PosHash[pos].addBuff(attrVar);
        }

        removeBuff(pos, attrVar) {
            if (this._PosHash[pos])
                this._PosHash[pos].removeBuff(attrVar);
        }

        getShip(pos) { return this._PosHash[pos] }
        get side() { return this._side }
        private _enterFlag = false;
        set enterFlag(val: boolean) { this._enterFlag = val }
        get ShipList() { return this._ShipList }
        private _ID: number;
        get ID(): number { return this._ID };
        private _Name: string;
        get Name(): string { return this._Name };
        private _Level: number;
        get Level(): number { return this._Level };
        private _FactionID: number;
        get FactionID(): number { return this._FactionID };
        private _AvatarID: number;
        get AvatarID(): number { return this._AvatarID };
        private _Count: number;
        get Count(): number { return this._Count };
        private _TotalCount: number;
        get TotalCount(): number { return this._TotalCount };
    }
}