/// <reference path='./FightViewBase.ts' />
namespace zt.battle {
    import GComponent = fairygui.GComponent;
    import Sprite = Laya.Sprite;
    import Event = Laya.Event;
    import Rectangle = Laya.Rectangle;
    export class SkipView extends FightViewBase {
        protected init() {
            this._ui.visible = this._model.mode == BattleMode.NORMAL;
            this._ui.onClick(this, this.onclick);
        }

        private onclick() {
            skipBattle(this._model.getParams())
        }

        protected draw(): void {
            //
        }

        protected get layout(): number {
            return Layout.RIGHT + Layout.TOP;
        }

        protected get offset(): number[] {
            // tslint:disable-next-line:no-magic-numbers
            return [FightViewBase.GAP - 3, FightViewBase.GAP + 40, 0, 0];
        }

    }
}
