namespace zt.battle {
    export class ActController {
        private _view: View;
        private _model: Model;
        constructor(view: View, model: Model) {
            this._view = view;
            this._model = model;
        }

        dispose() {
            ActTurn.dispose();
        }

        update(data: { Act, From, To, ActVar?,FactionID?})
         {
             let act :ActBase ;
            if(data.Act == 'Turn')
            {
                act = ActTurn.ins;
            }else
            {
                const cls: any = getDefinition(`zt.battle.Act${data.Act}`);
                if (!cls) {
                    console.log(`zt.battle.Act${data.Act}`)
                    return;
                }
                act = new cls();
            }
            act.model = this._model;
            act.mainView = this._view.ui.displayObject;
            act.container = this._view.container;
            act.attackerView = this._view.attacker;
            act.defenderView = this._view.defender;
            act.attacker = this._view.attacker.army;
            act.defender = this._view.defender.army;
            act.ActVar = data.ActVar;
            act.Crit = data['Crit'] || 0;
            act.to = data.To;
            if(data.Act == "Attack" || data.Act == "Skill")
            {
                if(ActTurn.ins && act.ActVar > 0)
                {
                    ActTurn.ins.to = data.From;
                    ActTurn.ins.play(data.Act)
                }
                const self = this ;
                Laya.timer.once(600,this,()=>
                {
                    self._view.attacker.shakeShip(data.To,data.Act,data.FactionID);
                });
            }
            if (data.From) {
                const fromView = data.From.Type == 1 ? this._view.attacker : this._view.defender;
                act.fromView = fromView;
                act.fromSide = fromView.army.side;
                act.fromArmy = fromView.army;
                act.fromPos = data.From.Pos;
                act.fromPoint = getPos(fromView.army.side, data.From.Pos);
            }
            if (data.To) {
                const toView = data.To[0].Type == 1 ? this._view.attacker : this._view.defender;
                act.toView = toView;
                act.toArmy = toView.army;
                act.toSide = toView.army.side;
            }
            act.play("attack",data.FactionID);
        }
    }
}