namespace zt.battle {
    import UIView = zt.app.UIView ;
    import GComponent = fairygui.GComponent;
    import GButton = fairygui.GButton;

    /**
     * view
     * @author wildfirecode
     * @since 0.0.3
     */
    export class View extends UIView {
        static BG_WIDTH = 1920;
        static BG_HEIGHT = 950;
        static LIMIT_WIDTH = 1600;
        static SCALE = 0.6;
        private _list: FightViewBase[];
        private _main: Sprite;
        private _bg: Sprite;
        private _attackerView: ArmyView;
        private _defenderView: ArmyView;
        private _container: Sprite;
        private _bound:Rectangle ;
        private lController:fairygui.Controller ;
        private rController:fairygui.Controller ;
        private bgCon:GComponent ;
        private close:GButton;
        private divisionLine:GComponent;
        private destinationOut:fairygui.GImage ;
        constructor(ui: GComponent)
        {
            super(ui);
        }

       public init() 
       {
           this._list = [];
           this.bgCon = this._ui.getChild('bg').asCom ;
           this._bg = this.bgCon.displayObject;
            this.close = this._ui.getChild('close') as GButton ;
            this._attackerView = new ArmyView('attacker');
            this._defenderView = new ArmyView('defender');
            this._container = new Sprite();
            this.divisionLine = this._ui.getChild('divisionLine').asCom;
            this.destinationOut = this._ui.getChild('destinationOut').asImage;     
            this.lController = this.divisionLine.getController('down');
            this.rController = this.divisionLine.getController('up');
       }

        public addEvent() 
        {
            const model = getModelByModuleName(AppEnum.battle);
            this.close.onClick(this, this.closeApp);
            zt.ctrl.GameCtrl.intance.on(EnumEventName.ARMY_UPDATE , this, this.onArmyUpdate);
        }

        public removeEvent()
        {
            this.close.offClick(this, this.closeApp);
            zt.ctrl.GameCtrl.intance.off(EnumEventName.ARMY_UPDATE , this, this.onArmyUpdate);
        }

        start() 
        {
            this._main = new Sprite();
            this._main.name = 'main';
            this._main.addChild(this._bg);
            this._ui.displayObject.addChild(this._main);

            for (let i = 0; i < this._ui.numChildren; i++) {
                const name: string = this._ui.getChildAt(i).name;
                if (name === 'bg') continue;
                const add = this.add(this._ui.getChildAt(i) as GComponent);
                this._ui.getChildAt(i).displayObject.pos(0, 0);
                this._main.addChild(this._ui.getChildAt(i).displayObject);
            }
            this._main.addChildren(this._attackerView, this._defenderView, this._container);
            this._container.addChild(this.destinationOut.displayObject);
            this.destinationOut.x = 565 ;
            this.destinationOut.y = 61 ;     
            // this.destinationOut.displayObject.blendMode = Laya.BlendMode.DESTINATIONOUT;
            this._container.addChild(this.divisionLine.displayObject);
            this._attackerView.pos(0,0);
            this._defenderView.pos(0,0);
            this._container.pos(0,0);
            this.loadBg();
            this.resize();
        }

        private loadBg() 
        {
            const model = getModelByModuleName(AppEnum.battle) as Model;
            this.bgCon.displayObject.loadImage(utils.getResourceURL(`fight/bg/${model.type}.jpg`))
        }


        private add(viewItem: GComponent) {
            const name = zt.utils.stringUtil.replaceFirstUper(viewItem.name);
            const cls: any = getDefinition(`zt.battle.${name}View`);
            if (cls === null) return;
            const model = this.model as Model ;
            const controller  = this.controller as Controller ;
            const view: FightViewBase = new cls(viewItem, controller , model);
            this._list.push(view);
            return view;
        }

        public resize() 
        {
            const stageW:number = Laya.stage.width ;
            if(stageW >= View.BG_WIDTH)
            {
                const toX = -(View.BG_WIDTH - Laya.stage.width )/2 ;
                const toY = -(View.BG_HEIGHT - Laya.stage.height )/2 ;
                this._main.x = toX ;
                this._main.y = toY ;
                for (const i of this._list) 
                {
                    i.resize();
                }
            }else
            {
                const scale: number = Laya.stage.width >= View.LIMIT_WIDTH ? 1 : View.SCALE;
                this._main.scale(scale,scale);
                for (const i of this._list) 
                {
                    i.resize();
                }
                this._bound = this._main.getBounds();
                const boundW = this._bound.width >= 1920 ? 1920 : this._bound.width ;
                const boundH = this._bound.height >= 1080 ? 1080 : this._bound.height ;
                const toX = (Laya.stage.width- this._bound.width)/2;
                const toY = (Laya.stage.height- this._bound.height)/2 + 50 ;
                this._main.x = toX ;
                this._main.y = toY ;
            }
        }

        private onArmyUpdate() 
        {
            let datal: Army;
            let datar: Army;
            const mdoel = this.model as Model;
            if (getAttackerSide(mdoel.type) == 'left') {
                datal = mdoel.Attacker;
                datar = mdoel.Defender;
            }
            else 
            {
                datar = mdoel.Attacker;
                datal = mdoel.Defender;
            }
            if (datal)
               this.lController.selectedIndex = getFrame(datal.FactionID);
            if (datar)
                this.rController.selectedIndex = getFrame(datar.FactionID);

        }

        disposeLoading() 
        {
            // utils.ArrayUtil.remove(this._loading, this._list);
            // this._loading.dispose();
            // this._loading = null;
        }

        dispose() 
        {
            this.removeEvent();
            for (const i of this._list) {
                i.dispose();
            }
        }
        
        get attacker() { return this._attackerView }
        get defender() { return this._defenderView }
        get container() { return this._container ;}
        get main() { return this._main }
    }
}