/// <reference path='./ActBase.ts' />
namespace zt.battle {
    import GComponent = fairygui.GComponent ;
    import Effect = zt.effects.Effect ;
    export class ActSkill extends ActBase {
        play() 
        {
            if (this.ActVar == 4 || this.ActVar == 1) 
            { 
                //4 1 debuff 
                // this.playBuffSkill();
            }
            else if (this.ActVar == 3 || this.ActVar == 2) {
                this.playSkill();
            }
        }


        playSkill() 
        {
            // console.log('attack start **********************')
            let attackX : number = 0 ;
            let attackY : number = 0 ;
            const actname:string = `Skill_${this.fromArmy.FactionID}_${this.ActVar}` ;
            const battleEffect:SysbattleEffectVo = Battle.SysBattleEffectP.getDataByAction(actname);
            const formView = this._fromSide == 'left' ? this.attackerView : this.defenderView ;
            const toView = this._fromSide == 'left' ? this.defenderView : this.attackerView ;
            const attack = formView.getShip(this._fromPos);

            let pos = {"x":0,"y":0};
            if(attack)
            {
                let shipName:string = attack.data.shipName;
                const attackShip:SysBattlePerformeVo = Battle.SysBattlePerformeP.getDataByShip(shipName);
                attackX = attackShip.AttackX;
                attackY = attackShip.AttackY;
                pos.x = attack.shipX 
                pos.y = attack.shipY ;
                  //
                  const raisePoint = {'x':pos.x + attackShip.HitX,'y':pos.y - 60};
                  const formPoint = {'x':pos.x + attackShip.HitY,'y':pos.y};
                  const raiseParam = getRaiseParam(formPoint,raisePoint,false,400);
                  const skillIcon = zt.utils.getResourceURL(this.skillIconUrl);
                  Tips.showRaisingImages(skillIcon,raiseParam);
            }
            const url:string = utils.getResourceURL(this.url) ;
            let effextX = this._fromSide == 'left' ? battleEffect.Lx : battleEffect.Rx ;
            let effextY = this._fromSide == 'left' ? battleEffect.Ly : battleEffect.Ry ;
            let aniX:number =  effextX[0] + pos.x + attackX ;
            let aniY:number =  effextY[0] + pos.y + attackY ;
            const rotation = this._fromSide == 'left' ? 0 : 180 ;
            const container = this.container ;
            const isContray  = this._fromSide == 'left' ? false : true ;
            const formAni =  zt.effects.createSkillEffect(url,aniX,aniY,isContray,length,container,true);
            formAni.playEffect(60);
            formAni.addFrame(battleEffect.Frame,Handler.create(this,()=>
            {
                pos.x = 0 ;
                pos.y = 0 ;
                for(let target of this._to)
                {
                    const defend = toView.getShip(target.Pos);
                    if(defend)
                    {
                        let shipName:string = defend.data.shipName;
                        const attackShip:SysBattlePerformeVo = Battle.SysBattlePerformeP.getDataByShip(shipName);
                        pos.x = defend.shipX + attackShip.HitX ; 
                        pos.y = defend.shipY + attackShip.HitY; 
                    }
                    aniX =  effextX[1] + pos.x + attackX ;
                    aniY =  effextY[1] + pos.y + attackY ;
                    const toAni =  zt.effects.createSkillEffect(url,aniX,aniY,isContray,length,container,false);
                    toAni.playEffect(60);
                }
            }));         
        }


        private get aniDirection() {
            return this._fromSide == 'left' ? 1 : 2;
        }

        private get url() {
            return `fight/skill/${this.ActVar}.json`;
        }

        private get skillIconUrl() {
            return `fight/icon/skill80/${this.ActVar}.png`;
        }
    }
}