/// <reference path='./ActBase.ts' />
namespace zt.battle {
    export class ActDecShield extends ActBase {
        //[{"To":[{"Type":0,"Pos":2}],"ActVar":-198,"Act":"DecHp"}]
        play() {
            const toArmy = this.getToArmyData(this._to[0].Type);
            toArmy.updateShipShield(this._to[0].Pos, this.ActVar);
            const toPoint = this.getToPoint(this._to[0].Pos);
            const type = this.Crit == 1 ?DamageNumsType.CRIT:DamageNumsType.DAMAGE
            this.playDamage(type);
        }
    }
}